import React, { ComponentType } from 'react';
import ReactDOM from 'react-dom';
import { connectedElement, Contextable } from '../../tipser-context';
import { EVENT_ADD_TO_CART } from '../../core';
import AddedToCartPopupDumb from './added-to-cart-popup-dumb';
import { Transition } from 'react-transition-group/esm';
import { TipserProductModel } from '../product/model';
import { isSSR } from '@tipser/tipser-sdk';

interface AddedToCartPopupPropTypes extends Contextable {
  onCheckoutClick: () => void;
}

interface AddedToCartPopupState {
  visible: boolean;
  product?: TipserProductModel;
}

const APPEARANCE_DURATION = 5_000;

class AddedToCartPopupNotConnected extends React.Component<AddedToCartPopupPropTypes, AddedToCartPopupState> {
  state = {
    visible: false,
    product: undefined,
  };

  elementContainer;

  componentWillMount(): void {
    if (isSSR()) {
      return;
    }
    this.props.context.events.register(EVENT_ADD_TO_CART, ({ product }: { product: TipserProductModel }) => {
      this.setState({ visible: true, product });
    });

    this.elementContainer = document.createElement('div');
    document.body.appendChild(this.elementContainer);
  }

  componentWillUnmount(): void {
    this.props.context.events.unregister(EVENT_ADD_TO_CART, this.onAddToCard);
    document.body.removeChild(this.elementContainer);
  }

  onAddToCard = ({ product }) => {
    this.setState({ visible: true, product });
  };

  onCheckoutClick = () => {
    this.setState({ visible: false }, () => {
      if (this.props.context.disableDialog) {
        window.location.href = this.props.context.tipserSdk.getPurchaseDialogUrl();
      } else {
        this.props.context.tipserSdk.openPurchaseDialog();
      }

      if (!this.props.context.openOldDialog && this.props.onCheckoutClick) {
        this.props.onCheckoutClick();
      }
    });
  };

  closePopup = () => {
    this.setState({ visible: false });
  };

  render() {
    if (isSSR()) {
      return null;
    }
    return ReactDOM.createPortal(
      <Transition
        in={this.state.visible}
        onEntered={this.closePopup}
        timeout={APPEARANCE_DURATION}
        unmountOnExit
        mountOnEnter
        exit={false}
      >
        <AddedToCartPopupDumb
          onClose={this.closePopup}
          onCheckoutClick={this.onCheckoutClick}
          product={this.state.product}
        />
      </Transition>,
      this.elementContainer
    );
  }
}

export const AddedToCartPopup: ComponentType<AddedToCartPopupPropTypes> = connectedElement(
  AddedToCartPopupNotConnected
);
export default AddedToCartPopup;
